.errorPage {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 100px;
    gap: 50px;
    &__number {
        font-size: 200px;
        color: #FF6060;
    }
    &__text {
        font-size: 40px;
        color: #FF6060;
    }
    &__link {
        color: black;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
}

@media (max-width: 768px) {
    .errorPage {
        &__number {
            font-size: 96px;
        }
        &__text {
            font-size: 18px;
        }
    }
}